<script>
import { mapActions, mapGetters } from "vuex";

import router from "../../../../router/index";
import HistoricalPerson from "./HistoricalPerson/HistoricalPerson.vue";

export default {
  data() {
    return {
      timePeriod: ["Name: Ascending", "Name: Descending"],
      selectedPeriod: "Name: Ascending",
      tab: 1,
    };
  },
  components: { HistoricalPerson },
  computed: {
    ...mapGetters("people", ["currentTopicPeople"]),
  },
  methods: {
    ...mapActions("people", ["fetchPeople", "setPersonForm", "clearPersonId"]),
    addPersonHandler() {
      this.clearPersonId();
      this.setPersonForm();
      router.push({
        name: "HistPeopleGeneral",
        path: "/historicalpeople/general",
      });
    },
  },
  created() {
    this.fetchPeople();
  },
};
</script>

